import React, {useState} from 'react'
import './Login.css'
import {Link, useHistory} from "react-router-dom";
import {auth} from './firebase';

function Login() {
    const history= useHistory();
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');

    const signIn = e=>{
        e.preventDefault()

        auth
        .signInWithEmailAndPassword(email, password)
        .then(auth=>{
            history.push('/')
        })
        .catch(error=>alert(error.message))
    }
    const register=e=>{
        e.preventDefault()
        
        auth
        .createUserWithEmailAndPassword(email, password)
        .then((auth)=>{
            if (auth){
                history.push('/');
            }
        })
        .catch(error => alert(error.message))
    }

    return (
        <div className='login'>
            <Link to ='/'>
            <img
            className='login__logo'
            src="https://nicolekrizek.com/wp-content/uploads/2018/04/amazon-4k-logo-qhd.jpg" alt=""/>
            </Link>

            <div className="login__container">
                <h1>Sign In</h1>

                <form action="">
                    <h5>E-Mail</h5>
                    <input type="text" value={email} onChange= {e => setEmail(e.target.value)}/>

                    <h5>Password</h5>
                    <input type="password" value={password} onChange= {e => setPassword(e.target.value)}/>

                    <button type='submit' onClick={signIn}
                    className='login__signinButton'>Sign In</button>
                </form>
                <p>
                    By signing-in you agree to amazons conditions of use and sale. 
                    Please see our privacy natice, our cookies notice and our interest-based ads.
                </p>
                <button onClick={register}
                 className='login__registerButon'>  Create Your acoount</button>
            </div>

        </div>
    )
}

export default Login
